.hamburgerContainer{
 padding-right:10px;
 color: darkcyan;
}

.menuIcon{
 color: darkcyan;
 height:35px;
 align-self:center;
}

.logo{
    height: 90px;
    width: 130px;
    top: 18px;
    position: absolute;
    z-index: 1;
}

.headerWrapper{
    width: 100%;
    height: 50px;
    background-color:white;
    display:flex;
    flex-wrap:wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 2.5px 2.5px 1.25px lightgrey;
    padding-top:5%;
}

.linkWrapper{
    display:none;
}

.contactBar{
    background-color:black;
    height:3%;
    position: absolute;
    width:100%;
    flex-basis:100%;
    color:white;
    display:flex;
    flex-direction:row;
    justify-content: space-between;
}



